<template>
  <div class="weatherApp">
    <div class="searchbar">
      <div class="form-group">
        <input type="text" placeholder="Search for a location" />
      </div>
      <button class="locateBtn" v-on:click="getLocation">
        <font-awesome-icon icon="map-marker-alt" />
      </button>
    </div>
    <h2 class="align-center">Weather</h2>
    <div class="para">
      <p class="align-center">Click on the GPS icon above to get current location.</p>
    </div>
    <div class="weather-wrapper">
      <header>
        <div class="date-wrapper">
          <p>Today, {{date}}</p>
        </div>

        <div class="container" v-if="notClicked">
          <!-- Loader for button -->
          <div class="loader">
            <div class="lds-ripple">
              <div></div>
              <div></div>
            </div>
          </div>

          <h2 class="nation-wrapper">
            <font-awesome-icon icon="map-marker-alt" />
            {{location}}, {{country}}
          </h2>
          <div class="info-wrapper">
            <img v-bind:src="icon" alt="image" />
            <h1>{{temp}} ºC</h1>
          </div>

          <p>{{description}}</p>
        </div>
      </header>
    </div>

    <!-- Component Wallaper -->
    <imagewrapper />
  </div>
</template>
  
<script>
import Imagewrapper from "./imagewrapper";

let dateWrapper = document.querySelector(".date-wrapper");
let today = new Date();
let day = today.getDate();
let month = today.getMonth();
let year = today.getFullYear();

// Switch case to convert months
switch (month) {
  case 0:
    month = "January";
    break;
  case 1:
    month = "February";
    break;
  case 2:
    month = "March";
    break;
  case 3:
    month = "April";
    break;
  case 4:
    month = "May";
    break;
  case 5:
    month = "June";
    break;
  case 6:
    month = "July";
    break;
  case 7:
    month = "August";
    break;
  case 8:
    month = "September";
    break;
  case 9:
    month = "October";
    break;
  case 10:
    month = "November";
    break;
  case 11:
    month = "December";
    break;
}

export default {
  name: "date",
  components: {
    Imagewrapper
  },
  data() {
    return {
      date: `${day} ${month} , ${year}`,
      location: "city",
      status: "",
      country: "country",
      temp: "",
      description: "...",
      icon: "",
      notClicked: false
    };
  },
  methods: {
    getLocation() {
    //   // Get ip in json format
    //   let getIP = `https://ipapi.co/json`;
    //   fetch(getIP)
    //     .then (resp => resp.json());

      var para = document.querySelector(".para");
      para.style.display = "none";
      if (window.navigator.geolocation) {
        window.navigator.geolocation.getCurrentPosition(position => {
          let latitude = position.coords.latitude;
          let longitude = position.coords.longitude;

          // Fetch Api for weather
          let link = `https://api.openweathermap.org/data/2.5/weather?lat=${latitude}&lon=${longitude}&APPID=8ba5f95c0efb1a0ddabb8595701ac9ec`;

          fetch(link)
            .then(data => data.json())
            .then(data => {
              this.icon =
                "https://openweathermap.org/img/w/" +
                data.weather[0].icon +
                ".png";
              console.log(data);
              this.location = data.name;
              this.country = data.sys["country"];
              this.temp = Math.floor(data.main["temp"] - 273.15);
              this.status = data.weather["description"];
              this.description = data.weather[0]["description"];
              this.notClicked = "true";
            });
        });
      } else {
        alert("Your browser does not support geolocation");
      }
    }
  }
};
</script>

